<template>
  <div class="good-list" v-loading="loading">
    <h2 class="title"></h2>

    <div class="query">
      <div class="query-type">
        <div
          class="query-type-item"
          v-for="item in qeuryStatusList"
          :class="{'active': query.onSale === item.value}"
          :key="item.label"
          @click="handerQueryType(item.value)">
          {{item.label}}
        </div>
      </div>

      <div class="query-list">
        <el-select
          class="query-list-item"
          v-model="query.appletId"
          placeholder=""
          @change="handleScreen">
          <el-option
            v-for="(item, key) in appletList"
            :key="item"
            :label="item"
            :value="key">
          </el-option>
        </el-select>

        <el-input v-model="query.goodsName" class="query-list-item keyword" placeholder=""></el-input>
        <el-button class="query-list-item" type="primary" @click="handleScreen"></el-button>
        <el-button class="query-list-item" type="primary" @click="exportData"></el-button>
      </div>
    </div>

    <el-table :data="tableData">
      <el-table-column label="ID" prop="goodsId"></el-table-column>
      <el-table-column label="" prop="goodsName" :show-overflow-tooltip="true"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ appletList[scope.row.appletId] }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{scope.row.onSale == 1 ? '' : ''}}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template>
          
        </template>
      </el-table-column>
      <el-table-column label="" prop="couponDiscount"></el-table-column>
      <el-table-column label="" prop="couponInfo.couponRemainQuantity"></el-table-column>
      <el-table-column label="" prop="formatMinNormalPrice"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ scope.row.pointsInfo.amount || 0 }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ scope.row.formatPromotionRate }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ (scope.row.pointsInfo.promotionRate || 0) / 100 }} / {{ (scope.row.cnyInfo.promotionRate || 0) / 100 }}
        </template>
      </el-table-column>
      <el-table-column label="" prop="salesTip"></el-table-column>
    </el-table>

    <div class="pagination">
      <el-pagination
        @current-change="handleCurrentChange"
        :hide-on-single-page="true"
        :current-page.sync="page.currentPage"
        :page-size.sync="page.pageSize"
        layout="prev, pager, next"
        :total="page.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPddGoodListApi, exportPddGoodListApi } from '../../../api/services/pointsMallManagement'
import config from '@/config'

export default {
  data() {
    return {
      loading: false, // loading
      query: {
        onSale: 2, // 
        appletId: '1', // 
        goodsName: '' // 
      },
      page: {
        total: 0, // 
        currentPage: 1,
        pageSize: 20
      },
      // 
      qeuryStatusList: [
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 0
        }
      ],
      // 
      appletList: config.appletList,
      tableData: [] // 
    }
  },
  created() {
    this.getDataList()
  },
  methods: {
    // 
    getDataList() {
      let _this = this

      _this.loading = true

      getPddGoodListApi(_this.getQueryData()).then(res => {
        let page = _this.page

        res.data.forEach(goodItem => {
          let couponInfo = goodItem.couponInfo,
            couponDiscount = couponInfo.couponDiscount, // 
            minNormalPrice = goodItem.minNormalPrice, // 
            salesConfig = goodItem.salesConfig,
            promotionRate = goodItem.promotionRate, // 
            pointsInfo = {}, // gbt
            cnyInfo = {} // 

          goodItem.couponDiscount = couponDiscount ? Number(couponDiscount) / 100 + '' : ''
          goodItem.formatMinNormalPrice = minNormalPrice ? Number(minNormalPrice) / 100 + '' : ''
          goodItem.formatPromotionRate = promotionRate / 10 + '%'

          salesConfig.forEach(salesConfigItem => {
            if (salesConfigItem.type === 'CNY') {
              cnyInfo = salesConfigItem
            } else {
              pointsInfo = salesConfigItem
            }
          })

          goodItem.pointsInfo = pointsInfo
          goodItem.cnyInfo = cnyInfo
        })

        _this.tableData = res.data
        page.total = Number(res.total)

        _this.loading = false
      }).catch(() => {
        _this.loading = false
      })
    },

    // 
    handerQueryType(status) {
      this.query.onSale = status
      this.handleScreen()
    },

    // 
    handleScreen(pageSize) {
      let _this = this

      _this.page.currentPage = 1
      _this.getDataList()
    },

    /**
     * 
     * @param { Boolean } type   screen  export 
     */
    getQueryData(type = 'screen') {
      let _this = this,
        page = _this.page,
        query = _this.query,
        goodsName = query.goodsName,
        onSale = query.onSale,
        params = {
          appletIds: [query.appletId],
          page: page.currentPage
        }

      if (goodsName) {
        params.goodsName = goodsName
      }

      if (onSale !== '') {
        params.onSale = onSale
      }

      if (type === 'export') {
        params.isExport = 1
        params.appletIds = String(params.appletIds)
      }

      return params
    },

    // 
    exportData() {
      exportPddGoodListApi(this.getQueryData('export'))
    },

    // 
    handleCurrentChange() {
      this.getDataList()
    }
  }
}
</script>

<style lang="less" scoped>
.good-list {
  padding: 0 16px;
  border-radius: 8px;
  background: #fff;

  & > .title {
    padding: 24px 0;
    font-size: 20px;
    line-height: 28px;
    color: #333;
  }

  .query {
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #333;
    padding-bottom: 16px;

    .query-type {
      display: flex;
      align-items: center;

      .query-type-item {
        position: relative;
        padding: 16px 0;
        font-size: 14px;
        font-weight: bold;
        letter-spacing: 0;
        cursor: pointer;

        & + .query-type-item {
          margin-left: 40px;
        }

        &::after {
          display: none;
          content: "";
          position: absolute;
          left: 0;
          right: 0;
          bottom: 0;
          height: 2px;
          background: #4D5AFF;
        }

        &.active {
          color: #4D5AFF;

          &::after {
            display: block;
          }
        }
      }
    }

    .query-list {
      display: flex;
      align-items: center;

      .query-list-item {
        & + .query-list-item {
          margin-left: 16px;
        }

        &.keyword {
          width: 164px;
        }
      }
    }
  }

  .pagination {
    padding-bottom: 16px;
    text-align: right;
  }
}
</style>
